.cardroot {
    display: flex;
    flex-direction: row;
    margin: 25px 20px;
    border: 5px solid gray;
    border-radius: 20px;
    padding: 10px 5px;
    background-color: black;
    box-shadow: 10px 10px gray;
    align-items: center;
    text-decoration: none;
    color: white;
}

.content {
    display: flex;
    flex-direction: column;
    flex: auto;
    text-align: left;
    margin: 5px 20px;
}

.rank {
    font-size:30px;
    margin: 10px 20px;
}

.score {
    margin-right: 20px;
    text-align: right;
}


.cardroot:hover{
    background-color: white;
    color: black;
}

.anime_title{
    color: inherit;
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
}

.score_content{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.score_img{
    height: 35px;
    margin-right: 10px;
}

.anime_img {
    height: 120px;
}

@media screen and (max-width: 734px) {
    .cardroot{
        min-width: 100%;
        margin: 25px 10px;
    }

    .anime_title{
        font-size: 20px;
    }

    .anime_img{
        height: 100px;
    }

    .score_img{
        height: 25px;
        margin-right: 5px;
    }

    .rank{
        font-size: 20px;
    }

    .score{
        font-size: 20px;
    }
    
}